import { NextRequest, NextResponse } from 'next/server';
import { postToPiServer } from '@/lib/piServer';

export async function completePOST(req: NextRequest) {
  try {
    const payload = await req.json();
    const { accessToken, paymentId, transactionId } = payload;

    if (!accessToken || !paymentId || !transactionId) {
      return NextResponse.json(
        { error: "Missing required params: accessToken, paymentId, transactionId" },
        { status: 400 }
      );
    }

    // Optionally: update your transaction here, then notify Pi server
    const piResult = await postToPiServer(
      'complete',
      paymentId,
      { paymentId, accessToken, transactionId },
      {
        logOk: (msg, res) => console.log(`Pi payment completed for paymentId=${paymentId}`, msg, res),
        logFail: (msg, err) => console.error(`Pi complete error for paymentId=${paymentId}`, msg, err),
      }
    );

    return NextResponse.json({
      result: 'completed',
      paymentId,
      piServer: piResult.response
    });
  } catch (error) {
    return NextResponse.json({ error: 'Invalid payload' }, { status: 400 });
  }
}
