import { NextRequest, NextResponse } from 'next/server';
import { postToPiServer } from '@/lib/piServer';

export async function incompletePOST(req: NextRequest) {
  try {
    const payload = await req.json();
    const { accessToken, paymentId, transactionId } = payload;

    if (!accessToken || !paymentId || !transactionId) {
      return NextResponse.json(
        { error: "Missing required params: accessToken, paymentId, transactionId" },
        { status: 400 }
      );
    }

    console.log("INCOMPLETE");
    // This is a stub for real review logic. Default action: complete.
    const userDecision: "complete" | "cancel" = "complete"; // TODO: Replace with your logic or DB result
    let piResult;

    if (userDecision === "complete") {
      piResult = await postToPiServer(
        'complete',
        paymentId,
        { paymentId, accessToken, transactionId },
        {
          logOk: (msg, res) => console.log(`Pi payment completed for incomplete paymentId=${paymentId}`, msg, res),
          logFail: (msg, err) => console.error(`Pi completion from incomplete failed for paymentId=${paymentId}`, msg, err),
        }
      );
    } else {
      piResult = await postToPiServer(
        'cancel',
        paymentId,
        { paymentId, accessToken },
        {
          logOk: (msg, res) => console.log(`Pi payment cancelled for incomplete paymentId=${paymentId}`, msg, res),
          logFail: (msg, err) => console.error(`Pi cancel from incomplete failed for paymentId=${paymentId}`, msg, err),
        }
      );
    }

    return NextResponse.json({
      result: userDecision,
      paymentId,
      piServer: piResult.response
    });
  } catch (error) {
    return NextResponse.json({ error: 'Invalid payload' }, { status: 400 });
  }
}
